*{
  margin:0;
  padding: 0;
}
html{
  background: #f5f5f5;
  height:100%;
}
body{
  min-height:100%;
  width:100%;
}
.wrapper{
  height: 100vh;
}
.container{
  margin:0 auto;
  width:80%;
  height:10vh;
  margin-left: 20%;
  background: mediumvioletred
}
.gallery-wrapper{
  display: grid;
  text-align: center;
  grid-template-columns: repeat(auto-fill, minmax(310px, 1fr));
  max-width: 84%;
  margin-left: 20%;
  padding-top: 2em;
}
/*-----LIST of CARDS*/
.avatar{
  -o-object-fit: cover;
     object-fit: cover;
}
.header-list-cards{
  text-align: center;
  padding: 0 3vw;
}
.title-cards{
  color: #025d54;
  font-weight: 900;
  margin: 0 auto;
  width: 65%;
  font-size: 2vh;
}
figure {
  margin: 0 .9em;
}
.w3-container{
  margin: 0 0 16% 0;
  background: #f3f3f3;

}
/*-------------*/
/* ---HEADER---*/
.header{
  background: #27175c;
  width: 20%;
}
li{
  list-style: none;
  padding: 1em 1.5em 0 0;
}
 a{
  color: #fff;
  text-decoration: none;
  font-size: 2.5vh;
}
.w3-bar-block, .w3-bar-item {
  padding: 6vh 0 0 0;
  text-align: center!important;
}
/*-----------------------*/
/*-----SEARCH-----------*/
form{
  height:100%;
  width:50%;
}
.search-bar{
  outline: none;
  border: none;
  border-bottom: 5px solid #fdcd09;
  width: 55%;
  text-align: center;
  font-size: 2.3vh;
  background: mediumvioletred;
  color: floralwhite;
  padding:5.4vh 0 0.5vh 0;
}
#filtersubmit{
  color: floralwhite!important;
  opacity: .7;
  -webkit-transition: ease-in .2s;
  -o-transition: ease-in .2s;
  transition: ease-in .2s;
  font-size: 3.5vh;
}
button{
  background: none;
  border: none;
  outline: none;
  border-bottom: 5px solid #fdcd09;
  font-size: 3.9vh;
  cursor: pointer;
}
#filtersubmit:hover{
  opacity:1;
}


